.flex{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-line-pack: start;
        align-content: flex-start;
}

.one-character{
  width:1.5rem;
  height:1.6rem;
  text-align:center;
  font-size: 1.2rem;
  line-height: 1.6rem;
  margin-bottom: 0.3rem;
}

@-webkit-keyframes progress-current {
  from {
    border-bottom: 2px solid #6CC3D5;
  }
  50% {
    border-bottom: 2px solid transparent;
  }
  to {
      border-bottom: 2px solid #6CC3D5;
    }

}

@keyframes progress-current {
  from {
    border-bottom: 2px solid #6CC3D5;
  }
  50% {
    border-bottom: 2px solid transparent;
  }
  to {
      border-bottom: 2px solid #6CC3D5;
    }
}

.current-chatacter{
  background-color: transparent;
  background-image: none;
  /*
   border-radius: 0.4rem;
   border: 1px solid #6CC3D5;*/

  -webkit-animation: progress-current 1s linear infinite;
  nimation: progress-current 1s linear infinite;
}

